* {
  padding: 0;
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; /*converting 10px to 1 rem instead of 16px from all browser default */
}

body {
  font-family:
    Gotham SSm A,
    Gotham SSm B,
    Arial,
    sans-serif;
}

.products {
  max-width: 114rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: max-content;
  grid-gap: 1rem;

  padding: 2rem;
}

.product_class {
  /* background-color: burlywood; */
  font-size: 2rem;
  font-weight: 500;
  color: black;
  border-radius: 5px;
  border: 1px solid #ccc;

  position: relative;
  overflow: hidden;

  /* //display for  */
  display: flex;
  align-items: center;
  justify-content: center;
}

.small-header {
  font-size: 1.5rem;
}

.nav-container {
  min-height: 2rem;
  /* background-color: #f8f8f8; */
  display: flex;
  align-items: center;
  padding: 3rem;
}

.nav-item:link,
.nav-item:visited {
  text-decoration: none;
  color: #414141;
  display: inline-block;
  padding: 0.5rem 1rem;
  margin-right: 0.3rem;
  font-size: 1.5rem;
}

.nav-item:hover {
  color: black;
}
.nav-item:link,
.nav-item:visited {
  padding: 0;
}
.basket {
  margin-left: auto;
  width: 15px;
  height: 15px;
}

.card-image {
  display: block;
  height: auto;
  width: 100%;
  object-fit: cover;
}

/* .card-title {
   position: absolute;
   z-index: 2;
   font-size: 2.5rem;
   color: white;
} */

.btn-primary {
  position: absolute;
  z-index: 2;

  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  width: 16.5rem;
  width: auto;
  height: 5rem;
  letter-spacing: 0.5px;
  line-height: 5rem;
  padding: 0 3rem 0 3rem;
  font-size: 1rem;
  font-weight: 400;
  margin: 0 auto;

  background-color: white;
  color: black;
  text-transform: uppercase;
  font-family: "Open Sans Condensed";
  font-weight: bolder;
  transition: all 0.3s;
}

.btn-primary:hover {
  background-color: black;
  color: white;
  border: 1px solid black;
}

.btn-secondary {
  width: 80%;
}
.footer-socials {
  /* max-width: 114rem;
  transform: translate(50%, 50%);

  transform: translate(-50%, -50%) */
  width: 100%;
  background-color: #2e2e2e;
  color: white;
  padding: 2rem 3rem;
  min-height: 3rem;
  display: flex;
  align-items: center;
}

.footer-socials-content {
  max-width: 114rem;
  font-size: 1.3rem;
  padding: 0 30px;
}

.SignUp-container {
  background-color: #f5f5f5;
  padding-top: 8rem;
  padding-bottom: 1.5rem;
}

.SignUp-Campaign {
  width: 60%;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.SignUp-Campaign > h3 {
  font-size: 3.5rem;
  font-weight: bold;
  line-height: 1.33;
  letter-spacing: -0.36px;
  text-transform: capitalize;
  padding-bottom: 0;
  text-align: center;
}

.SignUp-Campaign > p {
  margin: 2rem;
  font-size: 1.8rem;
  font-weight: normal;
  line-height: 1.43;
  letter-spacing: -0.28px;
  text-align: center;
}

.SignUp-Components {
  display: flex;
  align-items: center;
  width: 100%;
}

.SignUp-input {
  width: 100%;
  padding: 2rem 1rem;
  font-size: 1.2rem;
  color: #1a1a1a;
  border: 0;
  margin: 4rem auto;
  display: inline-block;
}

.SignUp-input-button {
  width: 6rem;
  height: 5.5rem;
  background-color: white;
  color: #1a1a1a;

  position: relative;
}

.SignUp-input-button-arrow,
.SignUp-input-button-search {
  background-repeat: no-repeat;
  background-size: 2rem 2rem;
  background-position: center;
}

.SignUp-input-button-arrow {
  background-image: url(data:image/svg+xml;base64,);
}

.SignUp-input-button-search {
  background-image: url(data:image/svg+xml;base64,);
}

.SignUp-input:focus {
  outline: none;
}

.SignUp-input::placeholder {
  color: #1a1a1a;
  font-size: 1.2rem;
}
