.Footer_Card_container {
  background-color: #fff;
  width: 100%;
  display: flex;
}

.Footer_Card_card,
.Footer_Card_Last {
  display: inline-block;
  background: linear-gradient(#2e2e2e, #2e2e2e);
  width: 33.3%;
}

.Footer_Card_Last_img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.Footer_Card_card:not(:last-child) {
  margin-right: 1.5rem;
}
